
import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

const initialState = {
  firstName: '', lastName: '', username: '', email: '', password: '',
  phoneCode: '', phoneNumber: '', country: '', city: '',
  pan: '', aadhar: ''
};

const citiesByCountry = {
  India: ['Delhi', 'Mumbai', 'Bangalore'],
  USA: ['New York', 'Chicago', 'San Francisco'],
  Australia: ['Sydney', 'Melbourne', 'Brisbane'],
};

const FormPage = () => {
  const [form, setForm] = useState(initialState);
  const [errors, setErrors] = useState({});
  const [showPassword, setShowPassword] = useState(false);
  const [isFormValid, setIsFormValid] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    const allFieldsFilled = Object.values(form).every(val => val.trim() !== '');
    const noErrors = Object.values(errors).every(err => err === '');
    setIsFormValid(allFieldsFilled && noErrors);
  }, [form, errors]);

  const validate = (name, value) => {
    switch (name) {
      case 'email':
        return /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(value) ? '' : 'Invalid email';
      case 'phoneNumber':
        return /^\d{10}$/.test(value) ? '' : 'Phone must be 10 digits';
      case 'pan':
        return /^[A-Z]{5}[0-9]{4}[A-Z]$/.test(value) ? '' : 'Invalid PAN';
      case 'aadhar':
        return /^\d{12}$/.test(value) ? '' : 'Aadhar must be 12 digits';
      default:
        return value.trim() === '' ? 'Required' : '';
    }
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setForm(prev => ({ ...prev, [name]: value }));
    setErrors(prev => ({ ...prev, [name]: validate(name, value) }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (isFormValid) {
      navigate('/success', { state: form });
    }
  };

  return (
    <div className="form-container">
      <h2>User Registration</h2>
      <form onSubmit={handleSubmit} noValidate>
        {['firstName', 'lastName', 'username', 'email'].map((field) => (
          <div key={field}>
            <label>{field.replace(/^\w/, c => c.toUpperCase())}:</label>
            <input type="text" name={field} value={form[field]} onChange={handleChange} />
            <span className="error">{errors[field]}</span>
          </div>
        ))}

        <div>
          <label>Password:</label>
          <input
            type={showPassword ? 'text' : 'password'}
            name="password"
            value={form.password}
            onChange={handleChange}
          />
          <button type="button" onClick={() => setShowPassword(!showPassword)}>
            {showPassword ? 'Hide' : 'Show'}
          </button>
          <span className="error">{errors.password}</span>
        </div>

        <div>
          <label>Country Code:</label>
          <input type="text" name="phoneCode" value={form.phoneCode} onChange={handleChange} />
        </div>

        <div>
          <label>Phone Number:</label>
          <input type="text" name="phoneNumber" value={form.phoneNumber} onChange={handleChange} />
          <span className="error">{errors.phoneNumber}</span>
        </div>

        <div>
          <label>Country:</label>
          <select name="country" value={form.country} onChange={handleChange}>
            <option value="">Select Country</option>
            {Object.keys(citiesByCountry).map(country => (
              <option key={country} value={country}>{country}</option>
            ))}
          </select>
        </div>

        <div>
          <label>City:</label>
          <select name="city" value={form.city} onChange={handleChange}>
            <option value="">Select City</option>
            {(citiesByCountry[form.country] || []).map(city => (
              <option key={city} value={city}>{city}</option>
            ))}
          </select>
        </div>

        <div>
          <label>PAN No:</label>
          <input type="text" name="pan" value={form.pan} onChange={handleChange} />
          <span className="error">{errors.pan}</span>
        </div>

        <div>
          <label>Aadhar No:</label>
          <input type="text" name="aadhar" value={form.aadhar} onChange={handleChange} />
          <span className="error">{errors.aadhar}</span>
        </div>

        <button type="submit" disabled={!isFormValid}>Submit</button>
      </form>
    </div>
  );
};

export default FormPage;
